body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  scroll-behavior: smooth;
}

section {
  position: relative;
  scroll-snap-align: start !important;
  overflow: hidden;
  height: 100vh;
}

.container {
  -ms-scroll-snap-type: y mandatory !important;
      scroll-snap-type: y mandatory !important;
  overflow-y: scroll !important;
  height: 100vh;
}

#Nav {
  /* position: fixed; */
  right: 5%;
  top: 5%;
  z-index: 3;
}
#Nav .burger {
  position: fixed;
  z-index: 99 !important;
  right: 5%;
  top: 5%;
  display: block;
}
#Nav .burger div {
  width: 25px;
  height: 3px;
  background-color: black;
  margin: 5px;
  transition: all 0.3s ease;
  box-shadow: 0 0 40px aliceblue, 0 0 30px lightskyblue, 0 0 20px lightskyblue;
}
#Nav .toggle .line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
  background-color: aliceblue;
}
#Nav .toggle .line2 {
  opacity: 0;
}
#Nav .toggle .line3 {
  transform: rotate(45deg) translate(-5px, -6px);
  background-color: aliceblue;
}

#Nav .nav-links {
  display: flex;
  position: fixed;
  right: 0%;
  top: 0%;
  padding-top: 60px;
  padding-bottom: 110px;
  height: 100vh;
  width: 30%;
  background-color: rgba(48, 107, 179, 0.95);
  z-index: 1;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  transform: translateX(100%);
  transition: all 0.5s ease-in-out;
}
#Nav .nav-active {
  transform: translateX(0%);
  box-shadow: 0 0 150px rgb(70, 40, 0);
}
#Nav .nav-links li {
  list-style: none;
  opacity: 0;
}

#Nav a {
  text-decoration: none;
  color: inherit;
  transition: all ease 0.3s;
}
#Nav a:hover {
  text-decoration: none;
  color: aliceblue;
}

@keyframes navLinkFade {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

@media screen and (max-width: 800px) {
  #Nav .nav-links {
    width: 70% !important;
  }
}

#Home {
  /* height: 200vh; */
  display: flex;
  height: 100vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding-top: 03%;
  padding-bottom: 03%;
}

#Home .text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 02.5%;
  margin-right: 02.5%;
  width: 30%;
  text-shadow: 0 0 75px lightskyblue;
}
#Home .text span {
  font-family: "Josefin Sans", sans-serif;
}
#Home .text h1 {
  font-family: "Comforter", cursive;
