#Sample {
  height: 100vh;
  position: relative;
}

#Sample .allContent {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
#Sample .btn {
  height: 50px;
  width: 50px;
  margin: 20px;
  border-radius: 50%;
  border: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: color ease-in-out 0.3s, background-color ease-in-out 0.3s,
    box-shadow ease-out 0.3s, height ease-out 0.3s, width ease-out 0.3s;
}

#Sample .btn:hover {
  background-color: black;
  color: aliceblue;
}
@keyframes clickAni1 {
  0% {
    box-shadow: none;
  }
  50% {
    box-shadow: 0 0 30px grey;
  }
  100% {
    box-shadow: none;
  }
}

#Sample .temp {
  box-shadow: 0 0 30px grey;
  transition: all ease 0.3s;
}
#Sample .temp > * {
  transition: all ease 0.3s;
  transform: scale(1.1, 1.1);
}

