#Sample {
  height: 100vh;
  position: relative;
}

#Sample .allContent {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
#Sample .btn {
  height: 50px;
  width: 50px;
  margin: 20px;
  border-radius: 50%;
  border: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: color ease-in-out 0.3s, background-color ease-in-out 0.3s,
    box-shadow ease-out 0.3s, height ease-out 0.3s, width ease-out 0.3s;
}

#Sample .btn:hover {
  background-color: black;
  color: aliceblue;
}
@keyframes clickAni1 {
  0% {
    box-shadow: none;
  }
  50% {
    box-shadow: 0 0 30px grey;
  }
  100% {
    box-shadow: none;
  }
}

#Sample .temp {
  box-shadow: 0 0 30px grey;
  transition: all ease 0.3s;
}
#Sample .temp > * {
  transition: all ease 0.3s;
  transform: scale(1.1, 1.1);
}

#Sample .heading_and_webCont {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#Sample .heading_and_webCont h2 {
  font-family: "Comforter", cursive;
  font-size: 4rem;
  font-weight: 200;
}
#Sample .websiteContainer {
  height: 60vh;
  width: 50vw;
  border-radius: 10px;
  /* background-color: aqua; */
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  transition: all ease-in-out 0.3s;
}

#Sample .websiteContainer img {
  height: 100%;
  width: auto;
  object-fit: contain;
  transition: all ease-in-out 0.3s;
}
#Sample .websiteContainer:hover img {
  height: 110%;
}
#Sample .websiteContainer h3 {
  position: absolute;
  bottom: 10px;
  left: 10px;
  transition: all ease-in-out 0.3s;
  font-family: "Shadows Into Light", cursive;
  letter-spacing: 1px;
  font-size: 4rem;
  color: aliceblue;
  text-shadow: 0 0 10px black, 0 0 10px black, 0 0 50px lightblue,
    0 0 50px lightblue, 0 0 50px lightblue, 0 0 50px lightblue;
}
#Sample .websiteContainer:hover h3 {
  font-size: 5rem;
  color: black;
  text-shadow: 0 0 10px lightblue, 0 0 10px lightblue, 0 0 70px darkslategray,
    0 0 50px darkslategray;
}

@keyframes rotRig {
  0% {
    transform: none;
    background-color: lightskyblue;
  }
  50% {
    background-color: blueviolet;
  }
  100% {
    transform: rotate(360deg);
    background-color: lightskyblue;
  }
}
@keyframes rotLef {
  0% {
    transform: none;
    fill: lightskyblue;
  }
  50% {
    fill: blueviolet;
  }
  100% {
    transform: rotate(360deg);
    fill: lightskyblue;
  }
}
#Sample .Fig._6.Container {
  position: absolute;
  background-color: lightskyblue;
  width: 10vw;
  height: 10vw;
  top: 5%;
  left: 5%;
  animation: rotRig 4s linear 0s infinite normal forwards;
}
#Sample .Fig._7.Container {
  position: absolute;
  fill: lightskyblue;
  width: 9vw;
  height: 9vw;
  bottom: 5%;
  right: 5%;
  animation: rotLef 6s linear 0s infinite reverse forwards;
}
#Sample .Fig._2.Container {
  position: absolute;
  fill: lightskyblue;
  width: 15vw;
  height: 15vw;
  top: unset;
  top: 30%;
  right: -3%;
  left: unset;
}

@media screen and (max-width: 800px) {
  #Sample .allContent {
    padding-bottom: 27%;
  }
  #Sample .websiteContainer {
    justify-content: center;
    align-items: center;
    height: 50vh;
    width: 60vw;
    border-radius: 20px;
  }
  #Sample .heading_and_webCont h2 {
    margin-bottom: 3%;
    font-size: 2rem;
  }
  #Sample .Fig._7.Container {
    bottom: 17%;
  }
  #Sample .btn {
    height: 50px;
    width: 50px;
    margin: 5px;
  }
  #Sample .websiteContainer img {
    height: auto;
    width: 100%;
    object-fit: cover !important;
  }
  #Sample .websiteContainer:hover img {
    height: 100%;
  }
  #Sample .websiteContainer h3 {
    font-size: 2rem;
  }
  #Sample .websiteContainer:hover h3 {
    font-size: 2rem;
    color: aliceblue;
    text-shadow: none;
  }
}
